

const _errorImage = require("~/assets/images/errorwifi.png");
const maxTries = 5;

export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    tries: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      phase: 1,
      warnpopup: false
    }
  },
  computed: {
    showDialog: function () {
      if (this.tries >= maxTries) {
        this.phase = 2;
      }
      return this.show;
    },
    errorImage() {
      return _errorImage;
    }
  },
  methods: {
    requestDownload(e) {
      console.log('some one requests a download');
      this.$emit('blobdownload', e);
    },
    closeClick(e) {
      //Home loose every thing, get back to home
      if (this.phase === 1) {
        //show popup sample
        this.warnpopup = true;
        return;
      }
      this.closeAnyWay(e);
    },
    closeAnyWay(e) {
      this.$emit('close', e);
      $nuxt.$router.replace($nuxt.localePath("/home"));
    },
    nextClick(e) {
      this.$emit('retry', e);
    }

  },
  mounted() {

  }
}
